<template>
  <h2>我是About的内容</h2>
</template>

<script>
  export default {
    name: 'About',
    // 通过路由规则，进行该组件时被调用
    beforeRouteEnter(to, from, next){
      // console.log('beforeRouteEnter', to, from, next) // to始终是当前About组件
      if(to.meta.isAuth){
        if(localStorage.getItem('school') === 'atguigu'){
          next()
        }else{
          alert('学校名不对，无权查看！')
        }
      }else{
        next()
      }
    },
    // 通过路由规则，离开该组件时被调用
    beforeRouteLeave(to, from, next){
      // console.log('beforeRouteLeave', to, from, next) // from始终是当前About组件
      next()
    }
  }
</script>